.title{
    background-color: white;
    font-size: large;
    font-weight: 200;
    text-align: center;
}
.orange{
    color: white;
    background-color: #043540;
    padding-top: 10px;
    padding-bottom: 10px;
}

.searchBar{
    margin-top: 5px;
    margin-right: 5px;
    margin-left: 5px;
    border: solid 1px white;
    border-radius: 4px;
    display: flex;
    padding: 5px;
    position: sticky;
    background-color: white;
}
.hint{
    margin-top: 2px;
    font-size: 14px;
    margin-left: 10px ;
    color: grey;
}
.filters{
    white-space: nowrap;
    display: flex;
    margin-top: 3px;
    margin-left: 5px;
    margin-right: 2px;   
    border: solid #043540 0.5px;
    border-radius: 5px;
    padding: 5px;
    position: sticky;
}  
.filtertext{
    font-size: 10px;
    color: #043540;
    font-weight: 300;
}
.filterRow{
    margin-right: 10px;
    overflow-x: scroll;
}

