.root{
	font-weight: 300;
}
.wallpaper {
  z-index: -1;
  position: relative;
  width: 100%;
  height: 400px;
}

.image {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 400px;
}

.overlay {
  position: absolute; 
  bottom: 0; 
  background: rgb(0, 0, 0);/* Black see-through */
  color: #f1f1f1; 
  width: 100%;
  transition: .5s ease;
  opacity:1;
  color: white;
  font-size: 20px;
  text-align: center;
  height: 100%;
}

.details{
	border-radius: 10px;
  margin-left: 5%;
  margin-right: 5%;
  background-color: transparent;
  border-radius: 2%;
  margin-top: -10%;
  margin-bottom: 10%;
  z-index: 1;
}

.avatar{
  margin-top: 30%;
}

.table{
	background-color: white;
  width: 100%;
  table-layout: fixed;
  border: solid 2px;
  border-color:white;
  border-radius: 5px;
}
.th{
  padding-top: 3%;
  color: black;
  font-size: 15px;
}
td{
	font-size: 10px;
	padding-bottom: 2%;
	color: grey;
	vertical-align: center;
}
.tr{
  text-align: center; 
  font-size: 2vh;
}

th{
	text-align: left;
}

.feat {
	/*background: var(--shade);*/
	position: absolute;
	top: 95vw;
	z-index: 50;
	width: 100vw;
	display: flex;
	justify-content: space-around;
}

.feat .list {
	background: var(--white);
	width: 250px;
	text-align: center;
	padding: 20px 0;
	border-radius: 10px;
	box-shadow: var(--bs);
	font-size: .8em;
}

.feat .list .main {
	font-size: 1.2em;
	font-weight: bold;
}

.container {
	position: relative;
}

.container .enroll {
	width: 90vw;
	margin: 5vw;
	border-radius: 10px;
	box-shadow: var(--bs);
	background: var(--blue);
	padding: 30px 0;
	text-align: center;
	text-transform: uppercase;
	color: var(--white);
	font-size: 1.2em;
	margin-top: 100px;
}

.container .head {
	width: 90vw;
	margin-right: 5vw;
	margin-left: 5vw;
	margin-top: 30px;
	margin-bottom: 10px;
	border-bottom: 1px solid #555;
	font-size: 10px;
	color: #555;
}

.container .info {
	width: 86vw;
	margin: 0 7vw;
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid #000;
	height: 60px;
	line-height: 60px;
	font-size: 17px;
	color: rgb(43, 44, 45);
}

.carousel {
	height: auto;
	display: flex;
	overflow-x: scroll;
	/*padding-right: 20px;*/
}

.carousel img {
	/*display: inline;*/
	height: 100vw;
	margin: 20px;
	border-radius: 5px;
}

.list{
	font-size: 14px;
	color: rgb(43, 44, 45);
	margin-right: 20px;
}
li{
	margin-bottom: 10px;
}

.listHeader{
	color: rgb(43, 44, 45);
	font-size: 16px;
	margin-left: 20px;
}

.restTd{
	color: rgb(43, 44, 45);
	font-size: 14px;
}

.tail{
	margin-top: 30px;
	font-size: 10px;
	padding-bottom: 2%;
	color: grey;
}

.gallery{
	width: 100%;
	height: 400px;
}

.courseTable{
	max-width: 100%;
	height: 40px;
	margin-left: 20px;
	
}

.offerCarousel{
	height: auto;
	display: flex;
	overflow-x: scroll;
	width: auto;
}

.offer{
	border-radius: 10px;
	padding: 5px;
	width: 200px;
	margin-left: 10px;
	margin-right: 10px;
	border: dotted 2px;
}

/*---------------------------------------------------------*/

.courses {
	display: flex;
	flex-wrap: wrap;
	padding: 0 4px;
	width: 92vw;
	margin-left: 4vw;
}

.course-col {
	flex: 46%;
	margin-right: 2%;
	max-width: 48%;
	padding: 0 4px;
}

.course-col .elem {
	margin-top: 20px;
	vertical-align: middle;
	width: 100%;
	border: 1px solid var(--blue);
	border-radius: 10px;
}

.courses .course-name {
	margin-left: 5%;
	width: 90%;
	height: 2em;
	line-height: 2em;
	font-size: 1.2em;	
}

.courses .inner-name {
	margin-left: 5%;
	width: 90%;
	height: 2em;
	line-height: 2em;
	border-bottom: 1px solid #0007;
}

.courses .inner {
	transition: .25s all ease;
}

.inner .fee, .inner .icon, .inner .details {
	display: none;
}

.courses .display {
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	z-index: 100;
	background: #000a;
}

.courses .display .ensemble {
	width: 100vw;
	height: 80vh;
	position: relative;
	top: 10vh;
	background: #fff;
}

.courses .display .fee, .courses .display .icon, .courses .display .details {
	display: block;
}

.courses .display .icon img {
	position: absolute;
	width: 25vw;
	height: 25vw;
	border-radius: 50%;
	overflow: hidden;
	margin: 5vw;
}

.courses .display .inner-name {
	font-size: 3em;
	border-bottom: 0;
	position: absolute;
	top: 12vw;
	left: 35vw;
}

.courses .display .fee {
	font-size: 1.5em;
	position: absolute;
	top: 15vw;
	right: 5vw;
	color: var(--blue);
}

.courses .display .details {
	font-size: 1.5em;
	line-height: 2em;
	position: absolute;
	width: 80vw;
	left: 10vw;
	top: 35vw;
}