.number{
    background-color: #04BFBF;
    color: white;
    font-weight: 200;
    padding: 10px;
    font-size: small;
}
.container{
    width: 100%;
    display: flex;
}
.item{
    background-color: white;
    margin: 5px 10px;
    border: solid #E6E6E6 0.5px;
    border-radius: 5px;
}
.name{
    margin: 10px 10px;
    font-weight: 300;
    font-size: 15px;
}
.map{
    margin-left: auto;
    margin-right: 10px;
    font-size: 7px;
    margin-top: 10px;
    color: #04BFBF;
}
.type{
    color: grey;
    font-size: 10px;
    margin: 0px 10px;
    align-self: center;
}
.fees{
    margin: 10px 10px;
    font-weight: 400;
    font-size: 13px;
    display: flex;
}
.showImage{
    object-fit: cover;
    height: auto;
    width: 100%;
    display: flex;
    overflow-x: scroll;
    border-radius: 5px;
}
a:link, a:visited {
    color: grey;
    text-decoration: none;
    cursor: auto;
  }
a:link:active, a:visited:active {
    color: grey;
}